.comics__list {
    margin-top: 45px;
}

.comics__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, 225px);
    justify-content: space-between;
    grid-row-gap: 55px;
    row-gap: 55px;
}

.comics__item {
    transition: 0.3s transform;

    &:hover {
        transform: translateY(-5px);
    }

    &__img {
        box-shadow: 5px 5px 10px rgb(0 0 0 / 25%);
        width: 225px;
        height: 345px;
    }

    &__name {
        margin-top: 10px;
        font-weight: bold;
        font-size: 14px;
        line-height: 18px;
    }

    &__price {
        margin-top: 5px;
        font-weight: bold;
        font-size: 14px;
        line-height: 18px;
        color: rgba(0, 0, 0, 0.6);
        text-transform: uppercase;
    }
}